"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { useAnalizaBuget } from "@/hooks/useApi";

interface CheltuieliInput {
  [key: string]: number;
}

const BugetPage = () => {
  const [venit, setVenit] = useState<number>(0);
  const [cheltuieli, setCheltuieli] = useState<CheltuieliInput>({
    Utilități: 0,
    Transport: 0,
    Alimentație: 0,
    Divertisment: 0,
    Altele: 0,
  });

  const { data: analizaData, isLoading, error, refetch } = useAnalizaBuget();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await refetch();
  };

  const updateCheltuieli = (categorie: string, valoare: string) => {
    setCheltuieli((prev) => ({
      ...prev,
      [categorie]: Number(valoare) || 0,
    }));
  };

  const totalCheltuieli = Object.values(cheltuieli).reduce((a, b) => a + b, 0);
  const economii = venit - totalCheltuieli;
  const procentEconomii = venit > 0 ? (economii / venit) * 100 : 0;

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold">Analiză și Recomandări Buget</h1>

      <div className="grid gap-6 md:grid-cols-2">
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Introducere Date</h2>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium mb-1">
                Venit Lunar (RON)
              </label>
              <Input
                type="number"
                value={venit || ""}
                onChange={(e) => setVenit(Number(e.target.value))}
                placeholder="Introduceți venitul lunar"
              />
            </div>

            <div className="space-y-4">
              <h3 className="text-lg font-medium">Cheltuieli Lunare</h3>
              {Object.keys(cheltuieli).map((categorie) => (
                <div key={categorie}>
                  <label className="block text-sm font-medium mb-1">
                    {categorie} (RON)
                  </label>
                  <Input
                    type="number"
                    value={cheltuieli[categorie] || ""}
                    onChange={(e) => updateCheltuieli(categorie, e.target.value)}
                    placeholder={`Cheltuieli ${categorie.toLowerCase()}`}
                  />
                </div>
              ))}
            </div>

            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? "Se analizează..." : "Analizează Bugetul"}
            </Button>
          </form>
        </Card>

        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Rezultate Analiză</h2>
          {error ? (
            <p className="text-red-500">
              A apărut o eroare în timpul analizei. Vă rugăm încercați din nou.
            </p>
          ) : (
            <div className="space-y-6">
              <div className="grid gap-4 grid-cols-2">
                <div>
                  <p className="text-sm text-gray-500">Total Venit</p>
                  <p className="text-2xl font-semibold">{venit} RON</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Total Cheltuieli</p>
                  <p className="text-2xl font-semibold">{totalCheltuieli} RON</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Economii</p>
                  <p className={`text-2xl font-semibold ${
                    economii >= 0 ? "text-green-600" : "text-red-600"
                  }`}>
                    {economii} RON
                  </p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Procent Economii</p>
                  <p className={`text-2xl font-semibold ${
                    procentEconomii >= 20 ? "text-green-600" : "text-yellow-600"
                  }`}>
                    {procentEconomii.toFixed(1)}%
                  </p>
                </div>
              </div>

              {analizaData?.data?.recomandări && (
                <div>
                  <h3 className="text-lg font-medium mb-2">Recomandări</h3>
                  <div className="space-y-2">
                    {analizaData.data.recomandări.map((rec: any, index: number) => (
                      <div
                        key={index}
                        className={`p-3 rounded-lg ${
                          rec.impact === "ridicat"
                            ? "bg-red-50 border border-red-200"
                            : rec.impact === "mediu"
                            ? "bg-yellow-50 border border-yellow-200"
                            : "bg-green-50 border border-green-200"
                        }`}
                      >
                        <p className="font-medium">{rec.tip}</p>
                        <p className="text-sm">{rec.descriere}</p>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          )}
        </Card>
      </div>
    </div>
  );
};

export default BugetPage;
